// Import all Dependencies
import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

// Import all Components
import MovieHeader from './MovieHeader';
import MovieDescription from './MovieDescription';
import MovieLink from './MovieLink';

// Import all Styles
import { Col } from '../../shared/styles/base/grid';

// Import all Assets
import bgCardOne from '../../shared/assets/images/bg-card-1.png';
import bgCardTwo from '../../shared/assets/images/bg-card-2.png';
import bgCardThree from '../../shared/assets/images/bg-card-3.png';
import bgCardFour from '../../shared/assets/images/bg-card-4.png';
import bgCardFive from '../../shared/assets/images/bg-card-5.png';
import bgCardSix from '../../shared/assets/images/bg-card-6.png';
import bgCardSeven from '../../shared/assets/images/bg-card-7.png';

// Import all Themes
import {
  COLOR_BACKGROUND,
  COLOR_WHITE,
} from '../../shared/styles/themes/colors';
import {
  BOX_SHADOW_PRIMARY,
  BOX_SHADOW_PRIMARY_HOVER,
} from '../../shared/styles/themes/box-shadow';

const ColumnWithBackground = styled(Col)`
  &:nth-child(1) > * {
    background: url(${bgCardOne});
  }

  &:nth-child(2) > * {
    background: url(${bgCardTwo});
  }

  &:nth-child(3) > * {
    background: url(${bgCardThree});
  }

  &:nth-child(4) > * {
    background: url(${bgCardFour});
  }

  &:nth-child(5) > * {
    background: url(${bgCardFive});
  }

  &:nth-child(6) > * {
    background: url(${bgCardSix});
  }

  &:nth-child(7) > * {
    background: url(${bgCardSeven});
  }
`;

const MovieContent = styled.div`
  display: block;
  background-color: ${COLOR_BACKGROUND};
  padding: 2rem;
  color: ${COLOR_WHITE};
  transform: perspective(1px) translateZ(0);
  box-shadow: ${BOX_SHADOW_PRIMARY};
  transition: all 0.3s;
  overflow: hidden;
  border-radius: 0.5rem;

  &:hover {
    box-shadow: ${BOX_SHADOW_PRIMARY_HOVER};
  }
`;

export default function MovieItem({ id, title, description, releaseDate }) {
  return (
    <ColumnWithBackground>
      <MovieContent>
        <MovieHeader id={id} title={title} releaseDate={releaseDate} />
        <MovieDescription description={description} />
        <MovieLink id={id} />
      </MovieContent>
    </ColumnWithBackground>
  );
}

MovieItem.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  releaseDate: PropTypes.string.isRequired,
};
